import { getAuthenticatedUser, checkExperienceAccess } from "@/lib/auth";
import { DashboardLayout } from "@/components/layout/DashboardLayout";
import { Card, CardContent } from "@/components/ui/card";

interface LayoutProps {
  children: React.ReactNode;
  params: Promise<{ experienceId: string }>;
}

export default async function ExperienceLayout({
  children,
  params,
}: LayoutProps) {
  const { experienceId } = await params;

  // Get authenticated user (handles token verification + profile sync)
  const auth = await getAuthenticatedUser();

  if (!auth) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center p-6">
        <Card>
          <CardContent className="py-8 text-center">
            <h1 className="text-xl font-semibold text-foreground mb-2">
              Authentication Required
            </h1>
            <p className="text-muted-foreground">
              Please access this page through your Whop community.
            </p>
          </CardContent>
        </Card>
      </div>
    );
  }

  // Check access to this experience
  const hasAccess = await checkExperienceAccess(experienceId, auth.userId);

  if (!hasAccess) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center p-6">
        <Card>
          <CardContent className="py-8 text-center">
            <h1 className="text-xl font-semibold text-foreground mb-2">
              Access Denied
            </h1>
            <p className="text-muted-foreground">
              You don&apos;t have access to this experience.
            </p>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <DashboardLayout
      experienceId={experienceId}
      user={{
        id: auth.user.id,
        name: auth.user.name,
        avatar: auth.user.avatar,
        sellerProfile: auth.user.sellerProfile
          ? { id: auth.user.sellerProfile.id }
          : null,
      }}
    >
      {children}
    </DashboardLayout>
  );
}
